<template>
  <div class="child">
    <div class="header_bottom" style="background-color: #004F9F;position: absolute;top:-100px;height:100px;z-index: 999;
transition: top 0.3s;
    -webkit-transition: top 0.3s;">
      <div>
        <el-menu
          :default-active="1" router
          id="header_bottom_menu"
          class="el-menu-demo"
          mode="horizontal"
          background-color="#004F9F"
          text-color="#fff"
          active-text-color="#000"
          style="height:100%;border: 0; background: rgba(15, 38, 69, 0.01);">
          <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
            {{ item.navItem }}
          </el-menu-item>
        </el-menu>
      </div>
      <div class="header_bottom_logo">
        <router-link :to="{path:'/'}"><img :src="img_logo" style="height: 95px" alt=""></router-link>
      </div>
    </div>

    <div class="center">
      <div class="menuChild" v-for="item in menuChild" @click="clickMenu(item.newsType)">
        {{item.name}}
      </div>
    </div>
    <div class="content" style="margin-top: 10px;">
      <div style="min-height: 512px">
        <div class="newsItem" v-for="item in tableData.data.records">
          <div style="float: right;width: 960px;">
            <p class="title" style="font-size: 16px;font-weight: bold">
              <router-link :to="{path:'/zxdt/xq', query:{subjectType:item.subjectType,id:item.id}}"  :title="item.title">{{item.title}}
              </router-link>
            </p>
            <p class="time">发布时间：{{item.publishTime}}</p>
            <p class="text" :title="item.abstractDesc">{{item.abstractDesc|ellipsis_1}}</p>
          </div>
          <div style="width: 200px;height: 120px;overflow: hidden;padding-top: 10px;padding-left: 10px">

            <img v-if="item.picUrl" :src=item.picUrl style="height: 100%;width: 100%;margin: 0;object-fit:cover;"
                 alt="">
            <img v-if="!item.picUrl" :src=img_新闻_默认 style="height: 100%;width: 100%;margin: 0;object-fit:cover;" alt="">
          </div>
        </div>
      </div>

      <div class="tabListPage" style="text-align: center;padding-top: 20px">
        <el-pagination background
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-size="10"
                       :total="totalCount"
                       layout="total, prev, pager, next, jumper">
        </el-pagination>
      </div>
    </div>
    <div class="bottom">
      <div style="width: 1200px;margin: 0 auto">
        <div style="float: right;padding-top: 30px">
          <img :src="this.$store.state.img6" style="width: 90px" alt="">
        </div>
        <div style="padding-top: 67px">
          <img :src="this.$store.state.img_bottom_address" style="width: 16px" alt=""> {{this.$store.state.address}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          <img :src="this.$store.state.img_bottom_postcode" style="width: 16px" alt=""> {{this.$store.state.postcode}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          版权所有 {{this.$store.state.copyright}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          备案号：{{this.$store.state.for_the_record}}
        </div>
        <div style="padding-top: 70px;text-align: center;font-size: 12px">
          {{this.$store.state.bottom_text}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          <a href="http://192.168.83.122:39119/admin/login" style="color: white">管理员登录</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "专项概况",
    filters: {
      ellipsis_1(value) {
        if (!value) return '';
        if (value.length > 130) {
          return value.slice(0, 130) + '...'
        }
        return value
      },
    },
    data() {
      return {
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 0,

        tableData: {
          "code": "0",
          "msg": "请求成功",
          "data": {
            "records": [
              {
                "id": "69",
                "title": "20201103测试",
                "newsType": "实时动态",
                "picUrl": "",
                "publishTime": "2020-11-03",
                "abstractDesc": "",
                "subjectType": "0"
              }
            ],
            "total": "6",
            "size": "10",
            "current": "1",
            "orders": [],
            "searchCount": true,
            "pages": "1"
          }
        },

        img_logo: require('@/assets/img/logo.png'),
        img_新闻_默认: require('@/assets/img/新闻_默认.jpg'),
        navList: [
          {name: '/', navItem: '首页'},
          {name: '/zxgk', navItem: '专项概况'},
          {name: '/zxdt', navItem: '专项动态'},
          {name: '/ldcg', navItem: '亮点成果'},
          // {name: '/pingtai', navItem: '共享平台'},
          {name: '/hzjl', navItem: '合作交流'},
          {name: '/zxgl', navItem: '专项管理'},
        ],
        menuChild: [
          {name: '综合新闻', newsType: 0},
          {name: '专项新闻', newsType: 1},
          {name: '实时动态', newsType: 2},
          {name: '通知公告', newsType: 3},
          {name: '航次动态', newsType: 4}
        ],
        newsType: 0,
        img_img: require('@/assets/img/图片.png'),
      }
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      clickNews(id) {
        this.$router.push({path: '/zxdt/xq', query: {id: id, subjectType: '0'}});
        // this.$router.push({path:'/home',query: {id:'1'}})
      },
      getData(n1) {
        // 这里使用axios，使用时请提前引入
        if (this.newsType == -1) {
          this.axios.get('http://192.168.83.122:39119/web/general/list', {
            // "newsType": this.newsType,
            // //新闻类型
            // "pageNumber": n1,
            // // 显示第几页
            // "pageSize": 10
            // // 每页显示的条数
          })
            .then(function (res) {
              //成功
              this.tableData = res.data;
              this.totalCount = parseInt(this.tableData.data.total);
            }.bind(this))
            .catch(function (err) {
              if (err.response) {
                //失败
                console.log(err.response)
              }
            }.bind(this))
        } else {
          this.axios.post('http://192.168.83.122:39119/web/news/list', {
            "newsType": this.newsType,
            //新闻类型
            "pageNumber": n1,
            // 显示第几页
            "pageSize": 10
            // 每页显示的条数
          })
            .then(function (res) {
              //成功
              this.tableData = res.data;
              this.totalCount = parseInt(this.tableData.data.total);
              for (let i = 0; i < this.tableData.data.records.length; i++) {
                this.tableData.data.records[i].subjectType = 0;
              }
            }.bind(this))
            .catch(function (err) {
              if (err.response) {
                //失败
                console.log(err.response)
              }
            }.bind(this))
        }
      },

      // 分页
      // 每页显示的条数
      // handleSizeChange(val) {
      //   // 改变每页显示的条数
      //   this.PageSize = val
      //   // 点击每页显示的条数时，显示第一页
      //   this.getData(val, 1)
      //   // 注意：在改变每页显示的条数时，要将页码显示到第一页
      //   this.currentPage = 1
      // },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData(val)
      },

      clickMenu(newsType) {
        let center = document.getElementsByClassName('center')[0];
        let menuDiv = center.getElementsByTagName('div');
        for (let i = 0; i < menuDiv.length; i++) {
          menuDiv[i].className = 'menuChild';
        }
        document.getElementsByClassName('menuChild')[newsType].className = 'this';
        this.newsType = newsType - 1;
        this.getData(1);
      },

      menu_change() {
        /*滚轮事件只有firefox比较特殊，使用DOMMouseScroll; 其他浏览器使用mousewheel;*/
        // firefox
        var direction;
        let menu = document.getElementsByClassName('header_bottom')[0];
        document.body.addEventListener("DOMMouseScroll", function (event) {
          direction = event.detail && (event.detail > 0 ? "mousedown" : "mouseup");
          if (document.documentElement.scrollTop > 100 || document.body.scrollTop > 100) {
            menu.style.top = '-100px';
            if (event.detail < 0) {
              menu.style.position = 'fixed';
              menu.style.top = '0';
            } else {

            }
          }
        });

        // chrome and ie
        document.body.onmousewheel = function (event) {
          event = event || window.event;
          direction = event.detail && (event.detail > 0 ? "mousedown" : "mouseup");
          if (document.documentElement.scrollTop > 100 || document.body.scrollTop > 100) {
            menu.style.top = '-100px';
            if (event.wheelDelta > 0) {
              menu.style.position = 'fixed';
              menu.style.top = '0';
            } else {

            }
          }
        };
      }
    },
    created() {

    },
    mounted() {

      this.menu_change();
      if (this.$route.query.menu) {
        switch (this.$route.query.menu) {
          case '0':
            this.clickMenu(0);
            break;
          case '1':
            this.clickMenu(1);
            break;
          case '2':
            this.clickMenu(2);
            break;
          case '3':
            this.clickMenu(3);
            break;
          case '4':
            this.clickMenu(4);
            break;

        }
      }else {
        this.clickMenu(0);
      }
    },
  }
</script>

<style scoped>
  #header_bottom_menu {
    width: 50%;
    float: right;
    font-size: 16px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    line-height: 30px;
  }

  #header_bottom_menu .el-menu-item {
    font-size: 16px;
    height: 79px;
    padding-top: 9px
  }

  #header_bottom_menu .el-menu-item:hover {
    background: #dee7f0 !important;
    color: #000 !important;
  }

  #header_bottom_menu .el-menu-item.is-active {
    background: #dee7f0 !important;
  }

  /deep/ .el-table::before {
    height: 0;
  }

  .newsItem {
    line-height: normal;
    border-bottom: 1px solid #ebeef5;
    height: 140px;
    color: #606266;
    transition: background-color 0.25s;
    -webkit-transition: background-color 0.25s; /* Safari */
  }

  .newsItem:hover {
    background-color: #f5f7fa;
  }

  a {
    text-decoration: none;
    letter-spacing: 1px;
  }

  a:link {
    color: black;
  }

  a:visited {
    color: black;
  }

  a:hover {
    color: dodgerblue;
  }
</style>



